.button {
    color: white;
    padding: var(--spacing-xs);

    &--bg-primary {
        background-color: var(--color-primary);
    }
}

/* primary */
.button--azure {
    background-color: var(--color-primary-azure);
}

/* secondary */
.button--navy {
    background-color: var(--color-secondary-navy);
}

.button--cerulean {
    background-color: var(--color-secondary-cerulean);
}

.button--emerald {
    background-color: var(--color-secondary-emerald);
}

.button--purple {
    background-color: var(--color-secondary-purple);
}

/* greyscale (si besoin) */
.button--grey {
    background-color: var(--color-grey);
    color: #000;
}